<template>
  <div class="showContent ocrContent">
    <requestAndHisSubMenu :routekey="routekey"></requestAndHisSubMenu>    
    <div class="box_request">
      <h3>Upload photos</h3>
      <uploadImage ref="uploadImage" class="marginleft"></uploadImage>
      <div class="box_line"></div>
      <div
        class="image_info"
      >Upload photos or just drag and drop face comparison picture here add 2 photos, JPG/JPEG/PNG ; 256*256 ~ 4096*4096, <2MB</div>
      <div class="image_submit">
        <a-button @click="gotoClear">Reset</a-button>
        <a-button type="primary" @click="gotoResult" htmlType="submit">Check</a-button>
      </div>
    </div>
    <p class="result" v-if="resultData">Result</p>
    <div class="box" v-if="resultData">
        <div class="infor_form">
        <div>
          <!-- 公司名 -->
          <label>NIK</label>
          <input type="text" :value="resultData.NIK"  readonly>
        </div>
        <div>
          <!-- Nama -->
          <label>Name</label>
          <input type="text" :value="resultData.Nama" readonly>
        </div>
        <div>
          <!-- Gol Darah 血型 -->
          <label>Bloodtype</label>
          <input type="text" :value="resultData['Gol Darah']" readonly>
        </div>
        <div>
          <!-- Agama 宗教-->
          <label>Religion</label>
          <input type="text" :value="resultData.Agama" readonly>
        </div>
        <div>
          <!-- Jenis Kelamin 性别-->
          <label>Gender</label>
          <input type="text" :value="resultData['Jenis Kelamin']" readonly>
        </div>
        <div>
          <!-- Tempat/Tgl  出生地/日期 -->
          <label>Religion</label>
          <input type="text" :value="resultData['Tempat/Tgl Lahir']" readonly>
        </div>
         <div>
          <!-- Tempat/Tgl  出生地/日期 -->
          <label>Birthday</label>
          <input type="text" :value="resultData['Tempat/Tgl Lahir']" readonly>
        </div>
         <div>
          <!-- RT/RW  出生地/日期 -->
          <label>RT/RW</label>
          <input type="text" :value="resultData['RT/RW']" readonly>
        </div>
        <div>
          <!-- PROVINSI   -->
          <label>Province</label>
          <input type="text" :value="resultData.PROVINSI" readonly>
        </div>
        <div>
          <!-- CITY   -->
          <label>City</label>
          <input type="text" :value="resultData.CITY" readonly>
        </div>
        <div>
          <!-- Kel/Desa  Village 村   -->
          <label>Village</label>
          <input type="text" :value="resultData['Kel/Desa']" readonly>
        </div>
        <div>
          <!-- Kecamatan District 街道   -->
          <label>District</label>
          <input type="text" :value="resultData.Kecamatan" readonly>
        </div>
        <div>
          <!-- Alamat   Address 地址 -->
          <label>Address</label>
          <input type="text" :value="resultData.Alamat" readonly>
        </div>
        <div>
          <!-- Pekerjaan   Occupation 工作 -->
          <label>Occupation</label>
          <input type="text" :value="resultData.Pekerjaan" readonly>
        </div>
        <div>
          <!-- Status PerKawinan   婚姻状况 -->
          <label>Marital status</label>
          <input type="text" :value="resultData['Status PerKawinan']" readonly>
        </div>
        <div>
          <!-- Berlaku Hingga   Berlaku Hingga 有效期至 -->
          <label>ExpiryDate</label>
          <input type="text" :value="resultData['Berlaku Hingga']" readonly>
        </div>
        <div>
          <!-- Kewarganegaraan   nationality  -->
          <label>Nationality</label>
          <input type="text" :value="resultData.Kewarganegaraan" readonly>
        </div>
        
      </div>
      
    </div>
  
  </div>
</template>
<script>
import requestAndHisSubMenu from "@/components/requestAndHisSubMenu";
import { error } from "util";
import uploadImage from "@/components/uploadImage/uploadImage";
export default {
  data() {
    return {
      routekey: ["idCard", "idCardHistory"],
      imgsrc: "",
      fileObj: "",
      resultData:"",
    };
  },
  components: {
    requestAndHisSubMenu,
    uploadImage
  },
  methods: {
    gotoClear() {
      this.$refs.uploadImage.deleteImage(0);
      this.$util.showOrHideDiv("result", false);
    },
    gotoResult() {
      this.fileObj = this.$refs.uploadImage.getImageFile();
      if (this.fileObj.fileImage) {
        if (this.fileObj.errImg == true) {
          this.$message.error(this.$t("verification.errorImg"));
        } else {
          console.log(this.fileObj);
          let params = {
            image_file: this.fileObj.fileImage,
          };
          this.$store.dispatch("OcrIDCard", params).then(
            res => {
              console.log("OcrIDCard res", res);
              this.resultData=res.data;
            },
            error => {
              console.log("OcrIDCard error", error);
            }
          );
        }
      } else {
        this.$message.error(this.$t("main.pleaseUploadPicture"));
      }
    }
  }
};
</script>
<style lang="less">
@import "../../css/page.less";
</style>



